#minimap span {
  color: white;
  font-size: 10px;
  margin: 4px;
  line-height: 12px;
}
#minimap .minimap-tab {
  grid-area: tab;
  background-color: #dddddd;
  border-bottom: none;
  display: flex;
}
#minimap .minimap-tab span {
  font-size: 10px;
  margin: 4px;
  color: #1a181d;
}
#minimap .minimap-body {
  grid-area: body;
  background: #dddddd;
}
#minimap .minimap-navbar {
  grid-area: navbar;
  background: #45404c;
}
#minimap .minimap-header,
#minimap .minimap-footer {
  background: #332f39;
  display: flex;
  align-items: center;
}
#minimap .minimap-header {
  grid-area: header;
}
#minimap .minimap-footer {
  grid-area: footer;
}
#minimap .minimap-favicon {
  padding: 0;
  margin: 2px;
  background: #fff;
}
#minimap .minimap-logo {
  margin-right: auto;
  margin-left: 20%;
}
#minimap .minimap-logo,
#minimap .minimap-official_img_header {
  height: 80%;
}

.ql-disabled .ql-editor {
  background: #eee;
}

.editor-container ul,
.editor-container ol {
  margin-bottom: 1rem;
}

select[multiple] {
  min-height: 100px;
}

.show_email {
  padding: 0 1rem;
}
.show_email h4 {
  padding-top: 1rem;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.1em;
  font-size: 0.9em;
  color: #97a2b2;
}
.show_email > div {
  padding: 1rem;
  background-color: #e8e8e8;
}
.show_email p {
  text-align: center;
  margin: 0;
}
.show_email #user_email + p {
  display: none;
  color: #97a2b2;
  font-style: italic;
}
.show_email #user_email:empty + p {
  display: block;
}

.survey-questions .question-divider {
  cursor: move;
}
.survey-questions .question-divider h2 {
  line-height: 32px;
}

.icon--move {
  vertical-align: middle;
}

.title-bar a.logo {
  color: #fff;
}
@media print, screen and (max-width: 39.99875em) {
  .title-bar {
    border-top: 2.125rem solid #060607;
  }
}

.tool-bar {
  text-align: right;
  position: absolute;
  right: 2rem;
  vertical-align: middle;
}
@media print, screen and (max-width: 39.99875em) {
  .tool-bar {
    position: initial;
  }
}
.tool-bar .dropdown.menu > li > a {
  background: transparent;
}
.tool-bar .is-dropdown-submenu {
  z-index: 2;
}
.tool-bar .user-login {
  vertical-align: middle;
  position: initial;
  text-align: left;
  display: inline-block;
  margin-top: 0;
}
@media print, screen and (max-width: 39.99875em) {
  .tool-bar .user-login {
    position: absolute;
    right: 2rem;
    margin-top: -1.2rem;
  }
}
.tool-bar .language-choose {
  position: initial;
  text-align: left;
  display: inline-block;
  vertical-align: middle;
}
.tool-bar .language-choose .is-dropdown-submenu-parent a {
  color: #fff;
}
.tool-bar .language-choose .is-dropdown-submenu-parent a::after {
  border-color: #fff transparent transparent !important;
}
.tool-bar .language-choose .is-dropdown-submenu li > a {
  color: #2c2930;
}
.tool-bar .language-choose .is-dropdown-submenu li > a:hover {
  background-color: white;
}
@media print, screen and (max-width: 39.99875em) {
  .tool-bar .language-choose {
    font-size: 0.8rem;
    position: absolute;
    top: -2.2rem;
    left: 0.5rem;
    margin-left: 0;
  }
  .tool-bar .language-choose .dropdown.menu > li.is-dropdown-submenu-parent > a::after {
    border-color: var(--secondary);
    border-width: 3px;
    right: 10px;
    margin-top: 0;
  }
}